import React, { useMemo } from 'react';
import { RouteComponentProps, useHistory } from 'react-router-dom';
import ContainerAuth from '../authContainers/Container';
import { HBButton } from 'hbBaseClass';

import styles from './SureApp.module.scss';

const SureApp: React.FC<RouteComponentProps> = () => {
  const history = useHistory();
  const { location } = history;
  const { state } = location;

  // check state is upload
  const isUpload = useMemo(() => state === 'upload', [state]);

  return (
    <ContainerAuth className="FormAuth-container sureApp">
      <div className={styles.wrapper}>
        <h1 className={styles.cardTitle}>
          {isUpload ? 'Perfekt! Vielen Dank f端r Ihre Angaben' : 'Sie haben sich\nerfolgreich registriert'}
        </h1>
        <p className={styles.content}>
          {isUpload
            ? 'Wir 端berpr端fen jetzt Ihre Daten und kontaktieren Sie in K端rze.'
            : 'Bitte kontrollieren Sie ihre Emails und aktivieren Sie Ihren Account indem Sie auf den zugesendeten Aktivierungslink klicken.'}
        </p>
        <div className={styles.image}></div>
        <HBButton
          color="violet"
          children={isUpload ? 'Verstanden' : 'Zur App'}
          btnClassName={styles.btnSubmit}
          onClick={() => history.push('sign-in')}
        />
      </div>
    </ContainerAuth>
  );
};

export default SureApp;
